<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>查询统计</el-breadcrumb-item>
      <el-breadcrumb-item>其它资金确认</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <div style='font-size: 14pt;font-weight:800'>
        筛选条件
      </div>
      <el-row :gutter="20" style='margin-top: 10px;margin-left: 20px'>
        <!--补贴类型-->
        <el-col :span="4">
          <el-row>
            <el-col :span="8">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'>补贴类型：</div>
            </el-col>
            <el-col :span="16">
              <el-select v-model="fundType" placeholder="请选择">
                <el-option
                  v-for="(item, index) in fundOptions"
                  :key="index"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <!--日期范围-->
        <el-col :span="9">
          <el-row>
            <el-col :span="4">
              <div style='font-weight: bold;margin-top: 10px;font-size: 10pt'>日期范围：</div>
            </el-col>
            <el-col :span="20">
              <el-date-picker
                v-model="dateRange"
                type="date"
                placeholder="选择日期">
              </el-date-picker>
            </el-col>
          </el-row>
        </el-col>
        <!--搜索按钮-->
        <el-col :span="6">
          <el-button type="primary" @click="getFundList">查询结果</el-button>
          <el-button type="danger" @click="clearSearch">清除筛选</el-button>
        </el-col>
      </el-row>

      <div style='font-size: 12pt; margin-top:25px' >
        <h3>查询结果</h3>
      </div>
      <el-tag :type="tagType" v-if="tagVisable" @click="showConfirmDialog" ref="tagRef">资金一</el-tag>
    </el-card>

    <!--   确认资金弹窗   -->
    <el-dialog title="资金确认详细信息" :visible.sync="confirmDialogVisible" width="50%">
     <h4 align='center' >{{fundDetailInfo.title}}</h4>
      <p style='text-indent: 2em;color: #409EFF'>{{fundDetailInfo.description}}</p>
      <span style="color: red"><span style="font-weight: bold">大写</span>：{{fundDetailInfo.capitalAmount}}</span><br>
      <span style="display: flex; justify-content: flex-end;  font-weight: bold">{{fundDetailInfo.capitalTime}}</span>
      <span slot="footer" class="dialog-footer">
          <el-button @click="confirmDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="confirmFunds">确 定</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>

export default {
  data() {
    return {
      //公司账号
      gjjzh: '',
      //确认资金弹窗标志
      confirmDialogVisible: false,
      // 查询的补贴类型
      fundType: '',
      // 补贴类型列表
      fundOptions: [
        {
          value: '售房款',
          label: '售房款'
        }, {
          value: '维修基金',
          label: '维修基金'
        }
      ],
      // 日期范围
      dateRange: '',
      // 控制资金按钮显示隐藏
      tagVisable: false,
      // 资金详细信息对象
      fundDetailInfo: {},
      // 资金tag标签类型
      tagType: 'primary'
    }
  },
  created() {
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
  },
  // 此时，页面上的元素，已经被渲染完毕了！
  methods: {
    // 获取资金列表
    async getFundList() {
      if (this.fundType === '') return this.$message.error('请选择资金类型')
      if (this.dateRange === '') return this.$message.error('请选择查询日期')
      const dateFormat = this.dateRange.getFullYear() + '-' + (this.dateRange.getMonth() + 1 + '').padStart(2,'0') + '-' +  (this.dateRange.getDate() + '').padStart(2,'0')
      const { data: res } = await this.$http.get('/qtzj/findQtzjYuE', { params: {
          gjjzh: this.gjjzh ,
          localDate: dateFormat,
          zjly: this.fundType
        }})
      if( res.code !== 200) return this.$message.error('查询失败')
      this.tagVisable =true
      this.fundDetailInfo = res.data
      console.log(res)
    },
    // 展示该资金的详细信息
    showConfirmDialog() {
      this.confirmDialogVisible = true
    },
    confirmFunds: function() {
      this.tagType = 'success'
      this.$message.success('确认成功！')
      this.confirmDialogVisible = false;
    },
    // 清空搜索项
    clearSearch() {
      this.dateRange = ''
      this.fundType = ''
      this.tagVisable = false
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-dialog__body {
  padding: 0 20px!important;
}
.el-tag {
  height: 60px;
  width: 120px;
  line-height: 60px;
  text-align: center;
  font-size: 15px;
  font-weight: bold;
}
</style>
